<template>
  <div>
    <div>
      <el-select v-model="searchtype" placeholder="请选择">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
      <el-input
        placeholder="请输入老人的信息进行搜索，可以直接回车搜索..."
        prefix-icon="el-icon-search"
        clearable
        style="width: 350px;margin-right: 10px"
        v-model="keyword"
        @keydown.enter.native="initEmps"
      ></el-input>
      <el-button icon="el-icon-search" type="primary">搜索</el-button>
      <el-button type="primary" style="margin-left:90%" @click="showaddoldview">添加老人</el-button>
      <!-- <el-dialog title="添加老人" :visible.sync="dialogVisible" width="30%">
        <el-form :rules="rules" ref="noform" :model="noform" label-width="125px">
          <el-form-item label="请输入老人姓名" prop="noldname">
            <el-input v-model="noform.noldname"></el-input>
          </el-form-item>
          <el-form-item label="请输入老人性别" prop="noldgender">
            <el-input v-model="noform.noldgender"></el-input>
          </el-form-item>
          <el-form-item label="请输入老人住址" prop="noldaddress">
            <el-input v-model="noform.noldaddress"></el-input>
          </el-form-item>
          <el-form-item label="请输入老人电话" prop="noldphone">
            <el-input v-model="noform.noldphone"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button :plain="true" @click="dialogVisible = false;exit();">取 消</el-button>
          <el-button :plain="true" type="primary" @click="dialogVisible = false;newolds();">确 定</el-button>
        </span>
      </el-dialog>-->
    </div>
    <div>
      <el-table :data="tableData | handlegender" border style="width: 95%">
        <!-- <el-table :data="tableData" border style="width: 95%"> -->
        <el-table-column fixed prop="oid" label="老人ID" width="100"></el-table-column>
        <el-table-column prop="name" label="姓名" width="200"></el-table-column>
        <el-table-column prop="gende" label="性别" width="200"></el-table-column>
        <el-table-column prop="address" label="地址" width="600"></el-table-column>
        <el-table-column prop="phone" label="联系方式" width="200"></el-table-column>
        <el-table-column fixed="right" label="操作" width="570">
          <template slot-scope="scope">
            <el-button type="primary" size="small" @click="showoldinfo(scope.row)">查看</el-button>
            <el-button
              type="danger"
              size="small"
              :disabled="!isRes(scope.row.responsibility)"
              @click="updoldinfo(scope.row)"
            >编辑</el-button>
            <el-button type="warning" size="small" @click="showhealthinfo(scope.row)">查看健康信息</el-button>
            <el-button
              type="success"
              size="small"
              :disabled="!isRes(scope.row.responsibility)"
              @click="updhealthinfo(scope.row)"
            >编辑健康信息</el-button>
            <el-button size="small" @click="add(scope.row)" v-show="isAdmin">添加负责人</el-button>
            <el-button
              size="small"
              type="danger"
              @click="deleteold(scope.row)"
              v-show="isAdmin"
            >删除老人</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog title="老人详情" :visible.sync="oldinfoVisible" width="70%">
        <div class="oldinfobg">
          <el-row type="flex">
            <el-col :span="2" :offset="8">
              <el-tag class="nametag" effect="dark">照片:</el-tag>
            </el-col>
            <el-col :span="10">
              <img
                :src="oldinfo.image"
                alt="task.image"
                :title="oldinfo.name"
                class="oldinfo-image"
              />
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="2" class="newold">
              <el-tag class="nametag" effect="dark">老人ID:</el-tag>
            </el-col>
            <el-col :span="4" class="newold">
              <el-tag class="infotag" type="danger">{{oldinfo.oid}}</el-tag>
            </el-col>
            <el-col :span="2" class="newold">
              <el-tag class="nametag" effect="dark">姓名:</el-tag>
            </el-col>
            <el-col :span="4" class="newold">
              <el-tag class="infotag" type="danger">{{oldinfo.name}}</el-tag>
            </el-col>
            <el-col :span="2" class="newold">
              <el-tag class="nametag" effect="dark">生日:</el-tag>
            </el-col>
            <el-col :span="4" class="newold">
              <el-tag class="infotag" type="danger">{{oldinfo.birthday}}</el-tag>
            </el-col>
            <el-col :span="2" class="newold">
              <el-tag class="nametag" effect="dark">性别:</el-tag>
            </el-col>
            <el-col :span="4" class="newold">
              <el-tag class="infotag" type="danger">{{oldinfo.gender | handleigender}}</el-tag>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="2" class="newold">
              <el-tag class="nametag" effect="dark">证件号:</el-tag>
            </el-col>
            <el-col :span="10" class="newold">
              <el-tag class="infotag" type="danger">{{oldinfo.identity}}</el-tag>
            </el-col>
            <el-col :span="2" class="newold">
              <el-tag class="nametag" effect="dark">地址:</el-tag>
            </el-col>
            <el-col :span="10" class="newold">
              <el-tag class="infotag" type="danger">{{oldinfo.address}}</el-tag>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="2" class="newold">
              <el-tag class="nametag" effect="dark">民族:</el-tag>
            </el-col>
            <el-col :span="4" class="newold">
              <el-tag class="infotag" type="danger">{{oldinfo.nation}}</el-tag>
            </el-col>
            <el-col :span="2" class="newold">
              <el-tag class="nametag" effect="dark">文化程度:</el-tag>
            </el-col>
            <el-col :span="4" class="newold">
              <el-tag class="infotag" type="danger">{{oldinfo.education}}</el-tag>
            </el-col>
            <el-col :span="2" class="newold">
              <el-tag class="nametag" effect="dark">联系方式:</el-tag>
            </el-col>
            <el-col :span="4" class="newold">
              <el-tag class="infotag" type="danger">{{oldinfo.phone}}</el-tag>
            </el-col>
            <el-col :span="2" class="newold">
              <el-tag class="nametag" effect="dark">居住状况:</el-tag>
            </el-col>
            <el-col :span="4" class="newold">
              <el-tag class="infotag" type="danger">{{oldinfo.single}}</el-tag>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="2" class="newold">
              <el-tag class="nametag" effect="dark">赡养人:</el-tag>
            </el-col>
            <el-col :span="4" class="newold">
              <el-tag class="infotag" type="danger">{{oldinfo.dependant}}</el-tag>
            </el-col>
            <el-col :span="3" class="newold">
              <el-tag class="nametag" effect="dark">赡养人联系方式:</el-tag>
            </el-col>
            <el-col :span="3" class="newold">
              <el-tag class="infotag" type="danger">{{oldinfo.dependantphone}}</el-tag>
            </el-col>
            <el-col :span="12" class="newold">
              <el-tag class="nametag" effect="dark">赡养人证件号:</el-tag>
              <el-tag class="infotag" type="danger">{{oldinfo.dependantidentity}}</el-tag>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="2" class="newold">
              <el-tag class="nametag" effect="dark">经济状况:</el-tag>
            </el-col>
            <el-col :span="4" class="newold">
              <el-tag class="infotag" type="danger">{{oldinfo.finance}}</el-tag>
            </el-col>
            <el-col :span="3" class="newold">
              <el-tag class="nametag" effect="dark">基本医疗保险:</el-tag>
            </el-col>
            <el-col :span="3" class="newold">
              <el-tag class="infotag" type="danger">{{oldinfo.insurance}}</el-tag>
            </el-col>
            <el-col :span="2" class="newold">
              <el-tag class="nametag" effect="dark">负责人ID:</el-tag>
            </el-col>
            <el-col :span="4" class="newold">
              <el-tag class="infotag" type="danger">{{oldinfo.responsibility}}</el-tag>
            </el-col>
          </el-row>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="oldinfoVisible = false" type="primary">确 定</el-button>
        </span>
      </el-dialog>

      <el-dialog title="修改老人信息" :visible.sync="updinfoVisible" width="90%">
        <el-form :model="old" :rules="rules" ref="oldform">
          <el-row>
            <el-col>
              <el-form-item label="老人照片" prop="uimg">
                <el-upload
                  class="avatar-uploader"
                  action="http://upload.qiniup.com"
                  :show-file-list="false"
                  :data="postData"
                  :on-success="handleAvatarSuccess"
                  :before-upload="beforeAvatarUpload"
                >
                  <img v-if="imageUrl" :src="imageUrl" class="avatar" />
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              <el-form-item label="姓名:" prop="name">
                <el-input
                  size="mini"
                  style="width: 150px"
                  prefix-icon="el-icon-edit"
                  v-model="old.name"
                  placeholder="请输入老人姓名"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item label="性别:" prop="gender">
                <el-radio-group v-model="old.gender">
                  <el-radio :label="1">男</el-radio>
                  <el-radio :label="0">女</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="出生日期:" prop="birthday">
                <el-date-picker
                  v-model="old.birthday"
                  size="mini"
                  type="date"
                  value-format="yyyy-MM-dd"
                  style="width: 150px;"
                  placeholder="出生日期"
                ></el-date-picker>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="民族:" prop="nation">
                <el-select v-model="old.nation" placeholder="民族" size="mini" style="width: 150px;">
                  <el-option
                    v-for="item in nations"
                    :key="item.id"
                    :label="item.name"
                    :value="item.name"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              <el-form-item label="联系方式:" prop="phone">
                <el-input
                  size="mini"
                  style="width: 200px"
                  prefix-icon="el-icon-phone"
                  v-model="old.phone"
                  placeholder="联系方式"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item label="地址:" prop="address">
                <el-input
                  size="mini"
                  style="width: 200px"
                  prefix-icon="el-icon-edit"
                  v-model="old.address"
                  placeholder="请输入居住地址"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="居住状况:" prop="single">
                <el-select
                  v-model="old.single"
                  placeholder="居住状况"
                  size="mini"
                  style="width: 150px;"
                >
                  <el-option v-for="item in singlestate" :key="item" :label="item" :value="item"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item label="经济状况:" prop="finance">
                <el-select
                  v-model="old.finance"
                  placeholder="经济状况"
                  size="mini"
                  style="width: 150px;"
                >
                  <el-option v-for="item in financestate" :key="item" :label="item" :value="item"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              <el-form-item label="身份证号码:" prop="identity">
                <el-input
                  size="mini"
                  style="width: 180px"
                  prefix-icon="el-icon-edit"
                  v-model="old.identity"
                  placeholder="请输入老人身份证号码"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item label="文化程度:" prop="education">
                <el-select
                  v-model="old.education"
                  placeholder="文化程度"
                  size="mini"
                  style="width: 150px;"
                >
                  <el-option v-for="item in educationlevel" :key="item" :label="item" :value="item"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="基本医疗保险:" prop="insurance">
                <el-radio-group v-model="old.insurance">
                  <el-radio label="职工医疗保险">职工医疗保险</el-radio>
                  <el-radio label="城镇居民医疗保险">城镇居民医疗保险</el-radio>
                  <el-radio label="新农合">新农合</el-radio>
                  <el-radio label="其他医疗保险">其他医疗保险</el-radio>
                  <el-radio label="无医保">无医保</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="6">
              <el-form-item label="赡养人姓名:" prop="dependant">
                <el-input
                  size="mini"
                  style="width: 150px"
                  prefix-icon="el-icon-edit"
                  v-model="old.dependant"
                  placeholder="赡养人姓名"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item label="赡养人身份证号:" prop="dependantidentity">
                <el-input
                  size="mini"
                  style="width: 150px"
                  prefix-icon="el-icon-edit"
                  v-model="old.dependantidentity"
                  placeholder="赡养人身份证号"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="赡养人联系方式:" prop="dependantphone">
                <el-input
                  size="mini"
                  style="width: 200px"
                  prefix-icon="el-icon-phone"
                  v-model="old.dependantphone"
                  placeholder="赡养人联系方式"
                ></el-input>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button :plain="true" @click="updinfoVisible = false;updexit();">取 消</el-button>
          <el-button @click="addorchageold();" type="primary">确 定</el-button>
        </span>
      </el-dialog>

      <el-dialog title="分配负责人" :visible.sync="addVisible" width="20%">
        <el-select v-model="rvalue" placeholder="请选择">
          <el-option v-for="rvalue in uids" :key="rvalue" :label="rvalue" :value="rvalue"></el-option>
        </el-select>
        <span slot="footer" class="dialog-footer">
          <el-button :plain="true" @click="addexit();">取 消</el-button>
          <el-button @click="oldinfoVisible = false;setRenponsibility(rvalue);" type="primary">确 定</el-button>
        </span>
      </el-dialog>

      <el-dialog title="查看健康信息" :visible.sync="shhVisible" width="75%">
        <el-table :data="shhData" border style="width: 80%">
          <el-table-column prop="weight" label="体重" width="150"></el-table-column>
          <el-table-column prop="bloodpressure" label="血压" width="150"></el-table-column>
          <el-table-column prop="heartbeat" label="心率" width="150"></el-table-column>
          <el-table-column prop="leftsight" label="左眼视力" width="150"></el-table-column>
          <el-table-column prop="rightsight" label="右眼视力" width="150"></el-table-column>
          <el-table-column prop="recognize" label="认知能力" width="150"></el-table-column>
          <el-table-column prop="mental" label="精神状况" width="150"></el-table-column>
        </el-table>
        <span slot="footer" class="dialog-footer">
          <el-button @click="shhexit();" type="primary">确 定</el-button>
        </span>
      </el-dialog>
      <el-dialog title="编辑健康信息" :visible.sync="uphVisible" width="50%">
        <el-form :rules="uphrules" ref="uphdata" :model="uphdata" label-width="150px">
          <el-form-item label="请输入老人体重" prop="weight">
            <el-input v-model="uphdata.weight"></el-input>
          </el-form-item>
          <el-form-item label="请输入老人血压" prop="bloodpressure">
            <el-input v-model="uphdata.bloodpressure"></el-input>
          </el-form-item>
          <el-form-item label="请输入老人心率" prop="heartbeat">
            <el-input v-model="uphdata.heartbeat"></el-input>
          </el-form-item>
          <el-form-item label="请输入老人左眼视力" prop="leftsight">
            <el-input v-model="uphdata.leftsight"></el-input>
          </el-form-item>
          <el-form-item label="请输入老人右眼视力" prop="rightsight">
            <el-input v-model="uphdata.rightsight"></el-input>
          </el-form-item>
          <el-form-item label="请输入老人认知能力" prop="recognize">
            <el-input v-model="uphdata.recognize"></el-input>
          </el-form-item>
          <el-form-item label="请输入老人精神状况" prop="mental">
            <el-input v-model="uphdata.mental"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button :plain="true" @click="uphexit();">取 消</el-button>
          <el-button @click="uphinfo();" type="primary">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { genUpToken, token } from "../../utils/qiniuToken";

export default {
  name: "PerTrain",
  filters: {
    handleigender(gender) {
      if (gender == 1) {
        return "男";
      } else if (gender == 0) {
        return "女";
      } else {
        return "未知";
      }
    },
    handlegender(tableData) {
      for (let i = 0; i < tableData.length; i++) {
        if (tableData[i].gender == "1") {
          tableData[i].gende = "男";
        } else if (tableData[i].gender == "0") {
          tableData[i].gende = "女";
        } else {
          tableData[i].gende = "未知";
        }
      }
      return tableData;
    }
  },
  data() {
    return {
      imageUrl: "",
      postData: {
        token: ""
      },
      //居住状况
      singlestate: ["独居", "双老同住", "子女同住"],
      //经济状况
      financestate: ["很宽裕", "比较宽裕", "大致够用", "比较困难", "非常困难"],
      nations: [
        {
          id: 1,
          name: "汉族"
        },
        {
          id: 2,
          name: "蒙古族"
        },
        {
          id: 3,
          name: "回族"
        },
        {
          id: 4,
          name: "藏族"
        },
        {
          id: 5,
          name: "维吾尔族"
        },
        {
          id: 6,
          name: "苗族"
        },
        {
          id: 7,
          name: "彝族"
        },
        {
          id: 8,
          name: "其他"
        }
      ],
      educationlevel: [
        "博士",
        "本科",
        "硕士",
        "大专",
        "高中",
        "初中",
        "小学",
        "其他"
      ],

      old: {
        oid: 0,
        name: "jjb",
        image: "",
        birthday: "1999-12-22",
        gender: 1,
        identity: "430602199912221234",
        address: "同济大学",
        nation: "汉族",
        education: "大学",
        phone: "18800291905",
        single: "独居",
        finance: "很富裕",
        insurance: "无医保",
        dependant: "abc",
        dependantphone: "15121031261",
        dependantidentity: "430602199010103017"
      },

      rules: {
        name: [{ required: true, message: "请输入姓名", trigger: "blur" }],
        gender: [{ required: true, message: "请输入性别", trigger: "blur" }],
        birthday: [
          { required: true, message: "请输入出生日期", trigger: "blur" }
        ],
        identity: [
          { required: true, message: "请输入身份证号码", trigger: "blur" },
          {
            pattern: /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/,
            message: "身份证号码格式不正确",
            trigger: "blur"
          }
        ],
        dependantidentity: [
          { required: true, message: "请输入身份证号码", trigger: "blur" },
          {
            pattern: /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/,
            message: "身份证号码格式不正确",
            trigger: "blur"
          }
