<template>
  <div class="demo">
    <el-button type="primary" style="margin-left:90%" @click="addevent()">添加事件</el-button>
    <el-dialog title="事件添加" :visible.sync="eventVisible" width="25%">
      <el-form :rules="rules" :model="form" label-width="125px">
        <el-form-item label="事件图片:" prop="eventimg">
          <el-upload
            class="avatar-uploader"
            action="http://upload.qiniup.com"
            :show-file-list="false"
            :data="postData"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload"
          >
            <img v-if="imageUrl" :src="imageUrl" class="avatar" />
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>

        <el-form-item label="事件日期:" prop="eventtime">
          <el-date-picker
            v-model="form.eventtime"
            size="mini"
            type="date"
            value-format="yyyy-MM-dd"
            style="width: 130px;"
            placeholder="事件日期"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="请输入事件内容" prop="eventinfo">
          <el-input v-model="form.eventinfo"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button :plain="true" @click="eventVisible = false;exit();">取 消</el-button>
        <el-button :plain="true" type="primary" @click="add">确 定</el-button>
      </span>
    </el-dialog>

    <schedule-calendar :events="events" :dateItemRender="itemRender"></schedule-calendar>
  </div>
</template>

<script>
import scheduleCalendar from "vue-schedule-calendar";
import { genUpToken, token } from "../../utils/qiniuToken";
export default {
  name: "PerSalary",
  components: {
    scheduleCalendar
  },
  data() {
    return {
      imageUrl: "",
      postData: {
        token: ""
      },

      events: [
        {
          uid: 11,
          date: "2019-12-1",
          context: "明天开会",
          status: 6
        }
      ],
      itemRender(item) {
        const h = this.$createElement;
        return h("span", item.uid + ": " + item.context);
      },
      eventVisible: false,
      form: {
        eventtime: "",
        eventinfo: ""
      },
      rules: {
        eventinfo: [
          { required: true, message: "请输入事件内容", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    addevent() {
      console.log("eventVisible" + this.eventVisible);
      this.eventVisible = true;
    },
    exit() {
      this.$message({
        showClose: true,
        message: "已取消"
      });
    },
    created() {
      // 显示为当月的数据
      const now = new Date();
      this.events = this.events.map(item => {
        const d = item.date.split("-")[2];
        item.date = `${now.getFullYear()}-${now.getMonth() + 1}-${d}`;
        return item;
      });
    },
    add() {
      var event = {
        uid: 1,
        date: this.form.eventtime,
        context: this.form.eventinfo
      };
      this.events.push(event);
      console.log(this.events);
      this.eventVisible = false;
    },
    handleAvatarSuccess(res) {
      this.imageUrl = "http://q38gdqayt.bkt.clouddn.com/" + res.key;
      console.log(this.imageUrl);
      // this.EncodedEntryURI = res.key;
    },
    beforeAvatarUpload(file) {
      const isIMG = ["image/jpeg", "image/png", "image/gif"].includes(
        file.type
      );
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isIMG) {
        this.$message.error("上传的头像必须是图片格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传头像图片大小不能超过 2MB!");
      }
      return isIMG && isLt2M;
    }
  },
  mounted() {
    this.postData.token = token();
    console.log(this.postData.token);
  }
};
</script>

<style>
.demo {
  display: flex;
  flex-direction: column;
  height: 700px;
  padding: 0 30px 30px;
  box-sizing: border-box;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>