<template>
  <div>
    <div style="margin-top: 10px;display: flex;justify-content: center">
      <el-input
        v-model="keywords"
        placeholder="通过ID搜索任务..."
        prefix-icon="el-icon-search"
        style="width: 400px;margin-right: 10px"
        @keydown.enter.native="doSearch"
      ></el-input>
      <el-button icon="el-icon-search" type="primary">搜索</el-button>
    </div>
    <div>
      <el-button type="primary" style="margin-left:90%" @click="newtask()">新建任务</el-button>
      <el-dialog title="新建任务" :visible.sync="dialogVisible" width="30%">
        <el-form :rules="rules" ref="tform" :model="tform" label-width="125px">
          <el-form-item label="请输入任务名" prop="ntaskname">
            <el-input v-model="tform.ntaskname"></el-input>
          </el-form-item>
          <el-form-item label="请输入任务描述" prop="ntaskinfo">
            <el-input v-model="tform.ntaskinfo"></el-input>
          </el-form-item>
          <el-form-item label="请输入任务对象" prop="ntaskold">
            <el-input v-model="tform.ntaskold"></el-input>
          </el-form-item>
          <el-form-item label="请输入任务周期" prop="ntaskfrequency">
            <el-select v-model="tform.ntaskfrequency" placeholder="请选择">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button :plain="true" @click="exit()">取 消</el-button>
          <el-button :plain="true" type="primary" @click="newtasks()">确 定</el-button>
        </span>
      </el-dialog>
    </div>
    <div class="task-container">
      <el-row style="width:100%">
        <el-col :span="6" v-for="(task,index) in tasks" :key="index" style="margin-bottom:40px">
          <el-card class="task-card">
            <div slot="header" class="clearfix">
              <span>{{task.tid}}</span>
            </div>
            <div>
              <div class="img-container">
                <img :src="task.image" alt="task.img" :title="task.name" class="task-image" />
              </div>
              <div class="userinfo-container">
                <div>任务名：{{task.title}}</div>
                <div>任务描述：{{task.description}}</div>
                <div>
                  任务状态：
                  <el-switch
                    v-model="task.finished"
                    active-text="已完成"
                    active-color="#13ce66"
                    inactive-color="#ff4949"
                    inactive-text="进行中"
                  ></el-switch>
                </div>
                <div>任务周期:{{task.frequency | handlefrequency}}</div>
                <el-button type="primary" class="task-button">转让</el-button>
              </div>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
export default {
  name: "PerEc",
  filters: {
    handlefrequency(frequency) {
      if (frequency == 1) {
        return " 每天一次";
      } else if (frequency == 7) {
        return " 每周一次";
      } else if (frequency == 30) {
        return " 每月一次";
      }
      return " 未设定";
    }
  },
  data() {
    return {
      keywords: "",
      options: [
        { value: 1, label: "每天一次" },
        { value: 7, label: "每周一次" },
        { value: 30, label: "每月一次" }
      ],
      tasks: [
        {
          tid: 2,
          title: "renwu1",
          description: "miaoshu1",
          image:
            "https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png",
          finished: true,
          frequency: 1,
          oname: "liHua",
          allocated: 0
        },
        {
          tid: 1,
          title: "renwu2",
          description: "miaoshu2",
          image:
            "https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png",
          finished: true,
          frequency: 1,
          oname: "liHua",
          allocated: 7
        }
      ],
      dialogVisible: false,
      tform: {
        ntaskname: "",
        ntaskinfo: "",
        ntaskfrequency: "",
        ntaskold: ""
      },
      rules: {
        ntaskname: [
          { required: true, message: "请输入任务名", trigger: "blur" }
        ],
        ntaskinfo: [
          { required: true, message: "请输入任务信息", trigger: "blur" }
        ],
        ntaskold: [
          { required: true, message: "请输入任务对象", trigger: "blur" }
        ],
        ntaskfrequency: [
          { required: true, message: "请输入任务频率", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    newtask() {
      this.dialogVisible = true;
    },
    newtasks() {
      this.dialogVisible = false;
      this.$message({
        showClose: true,
        message: "新建成功",
        type: "success"
      });
    },
    exit() {
      this.dialogVisible = false;
      this.$message({
        showClose: true,
        message: "已取消"
      });
    }
  }
};
</script>

<style scoped>
.img-container {
  width: 60%;
  margin-left: auto;
  margin-right: auto;
}
.task-button {
  float: right;
  margin-bottom: 4%;
}
.task-image {
  height: 200px;
  width: 200px;
}
.task-container {
  margin-top: 50px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.task-card {
  width: 90%;
  margin-bottom: 40px;
}
</style>